import { CalendarDaysIcon, HandRaisedIcon } from "@heroicons/react/24/outline";
import { Btn, Input } from "components/forms";
import { useAppContext } from "contexts";
import { useState } from "react";
import { Container } from "./container/Container";

export function Footer() {
  const { setMessage, setLoading } = useAppContext();
  const [value, setValue] = useState("");

  const onSubmit = (e) => {
    e.preventDefault();
    setLoading(true);
    setMessage({ message: "Subscribe confirmed", success: true });
    setTimeout(() => {
      setLoading(false);
      setValue("");
    }, 500);
  };
  return (
    <Container className="py-4">
      <div className="mx-auto grid max-w-2xl grid-cols-1 gap-x-8 gap-y-16 min-lg:max-w-none min-lg:grid-cols-2 sm:gap-8">
        <div className="max-w-xl min-lg:max-w-lg">
          <h2 className="text-3xl font-bold tracking-tight text-white min-sm:text-4xl">
            Subscribe to our newsletter.
          </h2>

          <form
            onSubmit={onSubmit}
            className="mt-6 flex items-center max-w-md gap-4 md:flex-col md:items-start"
          >
            <Input
              name="email"
              type="email"
              autoComplete="off"
              required
              value={value}
              onChange={(e) => setValue(e.target?.value)}
              className="min-w-0 flex-auto rounded-md border-0 bg-white/5 px-3.5 py-2 text-white shadow-sm ring-1 ring-inset ring-white/10 focus:ring-2 focus:ring-inset focus:ring-indigo-500 min-sm:text-sm min-sm:leading-6"
              placeholder="Enter your email"
              style_type="my-outline"
            />

            <Btn type="submit" className="px-3.5 h-full md:w-full">
              Subscribe
            </Btn>
          </form>
        </div>
        <dl className="grid grid-cols-1 gap-x-8 gap-y-10 min-sm:grid-cols-2 min-lg:pt-2 sm:gap-5">
          <div className="flex flex-col items-start">
            <div className="rounded-md bg-white/5 p-2 ring-1 ring-white/10">
              <CalendarDaysIcon
                className="h-6 w-6 text-white"
                aria-hidden="true"
              />
            </div>
            <dt className="mt-4 font-semibold text-white">Weekly articles</dt>
            <dd className="mt-2 leading-7 text-gray-400">
              Innovative cryptocurrency exchange with advanced financial
              services.
            </dd>
          </div>
          <div className="flex flex-col items-start">
            <div className="rounded-md bg-white/5 p-2 ring-1 ring-white/10">
              <HandRaisedIcon
                className="h-6 w-6 text-white"
                aria-hidden="true"
              />
            </div>
            <dt className="mt-4 font-semibold text-white">No spam</dt>
            <dd className="mt-2 leading-7 text-gray-400">
              We rely on blockchain technology to provide everything you need
              for wise trading and investment.
            </dd>
          </div>
        </dl>
      </div>
    </Container>
  );
}
