import { Btn } from "components/forms";
import { Title } from "components/reusable";
import { classes } from "helpers";
import { Link } from "react-router-dom";

// Images
import trading_img_green from "assets/images/home/trading-cart-green.svg";
import trading_img_red from "assets/images/home/trading-cart-red.svg";
import { useHomeContext } from "../Home";

const GridSystem = {
  container:
    "pl-8 pr-3 rounded-[8px] grid items-center min-w-[1000px] grid-cols-[79px_5fr_3fr_3fr_5fr_2fr] duration-300 hover:bg-[#242627]",
  header_item: "text-[13px] leading-[27px] text-[#8c8b90]",
  box: "flex items-center justify-center h-full w-fit px-1",
};

export const Table = () => {
  const { ref, coins } = useHomeContext();
  return (
    <div className="py-[var(--main-py)]" ref={ref}>
      <Title>World class trading platform</Title>
      <p className="text-lg text-[#aaabab] text-center mb-[4.8rem]">
        We rank top among first tier exchanges with highest number of listed
        crypto
      </p>
      <div className="overflow-x-auto">
        <div
          className={classes(
            "relative pb-5 pointer-events-none border-b rounded-none border-[#7ad1b3]",
            GridSystem.container
          )}
        >
          <span className={classes(GridSystem.box, GridSystem.header_item)}>
            #
          </span>
          <span className={classes(GridSystem.box, GridSystem.header_item)}>
            Name
          </span>
          <span className={classes(GridSystem.box, GridSystem.header_item)}>
            Price
          </span>
          <span className={classes(GridSystem.box, GridSystem.header_item)}>
            24h change
          </span>
          <span className={classes(GridSystem.box, GridSystem.header_item)}>
            Chart
          </span>
          <span className={classes(GridSystem.box, GridSystem.header_item)}>
            Trade
          </span>
        </div>
        <div className="relative pb-5">
          {!!coins?.length &&
            coins?.map((c, i) => <Row key={c?.name || i} {...c} index={i} />)}
        </div>
      </div>
    </div>
  );
};

const Row = ({ name, index, lastChange, price, url }) => {
  return (
    <div className={classes("py-[13px]", GridSystem.container)}>
      <div className={classes(GridSystem.box)}>{index + 1}</div>
      <div className={classes(GridSystem.box, "gap-3")}>
        <img
          className="w-11 h-11"
          src={process.env.REACT_APP_COIN_URL + url}
          alt=""
        />
        <span className="uppercase text-[#757880]">{name}</span>
      </div>
      <div className={classes(GridSystem.box)}>${price}</div>
      <div className={classes(GridSystem.box)}>{lastChange}%</div>
      <img
        src={lastChange >= 0 ? trading_img_green : trading_img_red}
        className={classes(GridSystem.box, "w-[138px] h-[45px]")}
        alt=""
      />
      <Link
        to={"/deposit"}
        state={{ coin_id: name }}
        className={classes(GridSystem.box, "h-[39px]")}
      >
        <Btn size="sm" btn_style="border" className="px-5">
          Trade
        </Btn>
      </Link>
    </div>
  );
};
