import { Register, Login, Terms } from "pages/auth";

export const authRoutes = {
  path: "auth",
  children: [
    {
      path: "register",
      element: <Register />,
      action: Register.action,
    },
    {
      path: "login",
      element: <Login />,
      action: Login.action,
    },
    {
      path: "terms",
      element: <Terms />,
    },
  ],
};
