import { Fragment, useEffect, useState } from "react";
import { Listbox, Transition } from "@headlessui/react";
import { ChevronUpDownIcon } from "@heroicons/react/20/solid";
import { useError, useFormRegister } from "hooks";
import { ErrorMessage } from "components/reusable";
import { useFormContext } from "react-hook-form";

export function Component({
  data,
  selected,
  name,
  error,
  onChange,
  defaultValue = null,
}) {
  const errorMessage = useError(name, error);
  const register = useFormRegister(name);
  const methods = useFormContext();
  const [value, setValue] = useState({});

  useEffect(() => {
    methods.setValue(name, value?.id);
    if (methods.formState.isSubmitted) {
      methods.trigger(name);
    }
  }, [value, name]);

  useEffect(() => {
    setValue(selected);
  }, [selected]);

  return (
    <>
      <input {...register} hidden type="text" />
      <Listbox
        {...register}
        value={value}
        onChange={(v) => {
          setValue(v);
          onChange?.(v);
        }}
        defaultValue={defaultValue}
      >
        <div className="relative mt-1 w-full">
          <Listbox.Button className="relative p-[var(--input-py)_var(--input-px)] w-full cursor-default rounded-lg bg-[rgb(88_92 96 / 43%)] border border-[#6d7277] text-left shadow-md focus:outline-none focus-visible:border-indigo-500 focus-visible:ring-2 focus-visible:ring-white focus-visible:ring-opacity-75 focus-visible:ring-offset-2 focus-visible:ring-offset-orange-300 sm:text-sm">
            {({ value }) => (
              <>
                <span className="block truncate">
                  {value?.name || "Placeholder"}
                </span>
                <span className="pointer-events-none absolute inset-y-0 right-0 flex items-center pr-2">
                  <ChevronUpDownIcon
                    className="h-5 w-5 text-gray-400"
                    aria-hidden="true"
                  />
                </span>
              </>
            )}
          </Listbox.Button>
          <Transition
            as={Fragment}
            leave="transition ease-in duration-100"
            leaveFrom="opacity-100"
            leaveTo="opacity-0"
          >
            <Listbox.Options className="z-10 absolute mt-1 max-h-60 w-full overflow-auto rounded-md bg-[rgb(88_92_96_/_1)] py-1 text-base shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none sm:text-sm">
              {!!data?.length ? (
                data?.map((v, i) => <Option key={i} value={v} />)
              ) : (
                <Option value={{ name: "No Options" }} />
              )}
            </Listbox.Options>
          </Transition>

          <ErrorMessage>{errorMessage}</ErrorMessage>
        </div>
      </Listbox>
    </>
  );
}

const Option = ({ value }) => {
  return (
    <Listbox.Option
      className={({ active, selected }) =>
        `relative flex gap-3 items-center cursor-default select-none min-h-[56px] p-[13px_21px] rounded-[5px_5px_0_0] ${
          active || selected ? "bg-white text-black" : "text-white"
        }`
      }
      value={value}
    >
      {({ selected }) => (
        <>
          {value?.img && <img className="w-7 h-7" src={value?.img} alt="" />}
          <span
            className={`block truncate ${
              selected ? "font-medium" : "font-normal"
            }`}
          >
            {value?.name}
          </span>
        </>
      )}
    </Listbox.Option>
  );
};

export const Select = Object.assign(Component, { Option });
