import React from "react";
import { Input } from "./Input";
import {} from "@heroicons/react/24/outline";
import { Btn } from "./btn/Btn";
import { classes } from "helpers";
import copy_img from "assets/icons/copy.svg";

export const CopyInput = ({ inputProps, value = "", onCopy }) => {
  const copyToClipboard = (text) => {
    navigator.clipboard.writeText(text).then(() => {
      onCopy?.(text);
    });
  };
  return (
    <div className="flex">
      <Input
        {...{
          ...inputProps,
          className: classes(inputProps?.className, "!rounded-r-none !h-full"),
        }}
        value={value}
        disabled
      />
      <Btn
        onClick={() => copyToClipboard(value)}
        className="shrink-0 px-5 h-full !rounded-l-none border-l-0 !border-[gray] flex items-center gap-1"
      >
        <img className="" src={copy_img} alt="" />
        Copy
      </Btn>
    </div>
  );
};
