import React from "react";
import img1 from "assets/images/home/benefit-img-1.svg";
import img2 from "assets/images/home/benefit-img-2.svg";
import img3 from "assets/images/home/benefit-img-3.svg";
import img4 from "assets/images/home/benefit-img-4.svg";

const benefits = [
  {
    img: img1,
    title: "Secure and Trusted",
    desc: "Your security is our top priority. We employ industry-leading security measures to safeguard your funds and personal information. You can trade with confidence knowing that your assets are protected.",
  },
  {
    img: img2,
    title: "Fast and Efficient",
    desc: "Our advanced trading engine ensures lightning-fast order execution, enabling you to take advantage of market opportunities as they arise. No more waiting for sluggish transactions  experience the speed and efficiency of our platform.",
  },
  {
    img: img3,
    title: "Wide Range of Crypto",
    desc: "Discover a diverse selection of cryptocurrencies on our platform. Whether you're interested in Bitcoin, Ethereum, or other popular altcoins, we've got you covered. Expand your portfolio and explore new investment opportunities.",
  },
  {
    img: img4,
    title: "Powerful Trading Tools",
    desc: "From real-time price charts and technical analysis indicators to advanced order types and customizable trading strategies, we provide the tools you need to make informed trading decisions.",
  },
];

export const Benefits = () => {
  return (
    <div className="py-[var(--main-py)] flex flex-col gap-[var(--main-py)]">
      <div className="flex justify-between gap-5 lg:flex-col">
        <div className="max-w-[257px] relative">
          <div className="flex items-center gap-2 absolute bottom-[calc(100%_+_10px)]">
            <div className="w-6 h-[1px] bg-[#979797]" />
            <span className="text-xl text-[#939495] w-fit">Benefits</span>
          </div>
          <p className="font-semibold text-[2.3rem] leading-[2.6rem]">
            Why customers choose us?
          </p>
        </div>
        <p className="text-lg leading-[26px] text-[#aaabab] max-w-[515px]">
          Get all the benefits of GitNeos crypto exchange in the palm of your
          hand. Here are a few reasons why you should choose us.
        </p>
      </div>
      <div className="grid grid-cols-4 gap-5 lg:grid-cols-2 md:!grid-cols-1">
        {benefits.map((d) => (
          <Box {...d} key={d?.title} />
        ))}
      </div>
    </div>
  );
};

const Box = ({ desc, img, title }) => {
  return (
    <div className="xl:max-w-full md:min-h-fit flex flex-col gap-4 p-[26px_27px] rounded-[5px] border border-transparent hover:border-solid hover:border-[#2f3233] hover:bg-[#242627] min-h-[346px] max-w-[272px] duration-300">
      <img className="w-10 h-10" src={img} alt="" />
      <div className="flex flex-col gap-3">
        <span className="text-lg font-semibold text-[#eee]">{title}</span>
        <p className="text-base text-[#aaabab]">{desc}</p>
      </div>
    </div>
  );
};
