import { classes } from "helpers";
import { useError, useFormRegister } from "hooks";
import { useId, useState } from "react";
import { ErrorMessage } from "../reusable";
import { useEffect } from "react";

export const Input = ({
  className = "",
  boxClassName = "",
  name,
  error,
  errorClassName,
  placeholder,
  onChange,
  value,
  style_type = "", // ["my-outline"]
  ...props
}) => {
  const uniqueId = useId();
  const [show, setShow] = useState(false);
  const [cur_value, setValue] = useState("");

  const errorMessage = useError(name, error);
  const register = useFormRegister(name);

  const handleShow = () => {
    setShow(true);
  };

  useEffect(() => {
    setValue(value);
  }, [value]);

  return (
    <div className={"flex w-full relative " + boxClassName}>
      <label
        htmlFor={uniqueId}
        className={`main-input-placeholder ${
          show ? "top-[20%] !text-sm" : "top-1/2"
        } `}
      >
        {placeholder}
      </label>
      <input
        {...{ ...props, placeholder }}
        className={classes("main-input", style_type, className, show && "show")}
        {...register}
        id={uniqueId}
        value={value}
        onChange={(e) => {
          register?.onChange?.(e);
          onChange?.(e);
          setValue?.(e.target.value);
          handleShow?.();
        }}
        onFocus={handleShow}
        onBlur={(e) => {
          register?.onBlur?.(e);
          !cur_value && setShow?.(false);
        }}
        name={name}
      />
      <ErrorMessage className={errorClassName}>{errorMessage}</ErrorMessage>
    </div>
  );
};
