import { createContext, useContext, useState } from "react";

const AppContext = createContext({
  message: null,
  setMessage: null,
  makeAuthorized: (bool) => {},
  auth: {
    isAuth: false,
  },
  loading: false,
  setLoading: (p) => {},
});

export const useAppContext = () => useContext(AppContext);

export const AppProvider = ({ children }) => {
  const [message, setMessage] = useState(null);
  const [auth, setAuth] = useState({
    isAuth: false,
  });
  const [loading, setLoading] = useState(false);

  const makeAuthorized = (bool = true) => {
    setAuth((p) => ({ ...p, isAuth: bool }));
  };
  return (
    <AppContext.Provider
      value={{ loading, setLoading, message, setMessage, makeAuthorized, auth }}
    >
      {children}
    </AppContext.Provider>
  );
};
