import { Btn } from "components/forms";
import phone_img from "assets/images/Phone.png";
import open_img from "assets/images/open.png";
import coins_img from "assets/images/coins.png";
import buy_img from "assets/images/buy.png";
import { useHomeContext } from "../Home";

export function Preface() {
  const { toCoinsTable } = useHomeContext();

  return (
    <div className="relative">
      {/*  className="pb-80 pt-16 min-sm:pb-40 min-sm:pt-24 min-lg:pb-48 min-lg:pt-40" */}
      <div className="pb-32 min-lg:pb-36">
        <div className="flex justify-between gap-5 lg:flex-col items-center mx-auto">
          <div className="flex flex-col gap-5 items-start lg:items-center max-w-[485px]">
            <h1 className="tracking-tight text-inherit text-6xl font-medium sm:text-2xl lg:text-center">
              <span className="text-[var(--green-light)]">GitNeo: </span>
              Secure, Fast, and Reliable Crypto Trading
            </h1>
            <p className="mt-4 text-xl text-gray-500 lg:text-center">
              We provide you with a seamless and user-friendly platform to trade
              and invest in cryptocurrencies. Whether you're a beginner or an
              experienced trader, our powerful tools and intuitive interface
              make it easy for you to navigate the exciting world of digital
              assets.
            </p>
            <Btn onClick={toCoinsTable}>Shop Collection</Btn>
          </div>
          <div className="relative min-w-[338px]">
            {/* Decorative image grid */}
            <img className="max-h-[686px]" src={phone_img} alt="" />
            <img
              className="w-[262px] h-[79px] absolute top-[192px] left-[-121px] xl:top-[135px] lg:top-[192px] md:left-[-60px]"
              src={open_img}
              alt=""
            />
            <img
              className="w-[264px] h-[182px] absolute top-[169px] right-[-157px] xl:top-[255px] xl:right-[-105px] xl:right-[-45px] lg:right-[-157px] md:right-[-75px] md:top-[320px]"
              src={coins_img}
              alt=""
            />
            <img
              className="w-[262px] h-[125px] absolute left-[-81px] -bottom-1.5 md:left-[-15px] md:-bottom-5"
              src={buy_img}
              alt=""
            />
          </div>
        </div>
      </div>
    </div>
  );
}
