import React from "react";
// images
import img1 from "assets/images/home/copy.webp";
import img2 from "assets/images/home/deposit.webp";
import img3 from "assets/images/home/launchpad.webp";
import img4 from "assets/images/home/more.webp";
import img5 from "assets/images/home/zero.webp";

// Swiper
import "swiper/css";
import "swiper/css/pagination";
import { Pagination } from "swiper/modules";
import { Swiper, SwiperSlide } from "swiper/react";

const iamges = [img1, img2, img3, img4, img5, img1, img2, img3, img4, img5];

export const Slider = () => {
  return (
    <Swiper
      slidesPerView={1}
      spaceBetween={24}
      breakpoints={{
        640: { slidesPerView: 2 },
        768: { slidesPerView: 4 },
        1024: { slidesPerView: 5 },
      }}
      pagination={{ clickable: true }}
      loop={true}
      modules={[Pagination]}
      className="mySwiper py-5 w-full"
    >
      {iamges?.map((img, i) => (
        <SwiperSlide key={i}>
          <img src={img} alt="" />
        </SwiperSlide>
      ))}
    </Swiper>
  );
};
