import axios from "axios";
import { userHandler } from "helpers";

const baseURL = process.env.REACT_APP_API_URL;

const instance = axios.create({
  baseURL,
  withCredentials: true,
  headers: {
    "Content-Type": "application/x-www-form-urlencoded",
    "Access-Control-Allow-Origin": baseURL,
    "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE,PATCH,OPTIONS",
  },
});

instance.interceptors.request.use(
  (config) => {
    if (userHandler.getToken()) {
      config.headers.Authorization = `Bearer ${userHandler.getToken()}`;
    }
    return config;
  },
  (err) => {
    throw err;
  }
);

instance.interceptors.response.use(
  (config) => {
    const data = config.data;
    if (data?.access_token) {
      userHandler.setToken(config.data.access_token);
    }
    return config;
  },
  async (err) => {
    if (err?.response?.data?.message === "jwt expired") {
      console.log("Detected: jwt expired");
      try {
        const res = await axios.get(`${baseURL}auth/refresh`, {
          withCredentials: true,
        });
        const access_token = res.data?.access_token;
        if (access_token) userHandler.setToken(access_token);

        return instance.request(err.config);
      } catch (error) {
        console.log(error);
        throw error;
      }
    }
    console.error(err);
    throw err;
  }
);

export default instance;
