import { InformationCircleIcon } from "@heroicons/react/24/outline";
import { yupResolver } from "@hookform/resolvers/yup";
import { Btn, Input, Select } from "components/forms";
import { withdraw } from "contsants/withdraw";
import { useMemo, useState } from "react";
import { FormProvider, useForm } from "react-hook-form";
import { useFormAction, useLoaderData, useSubmit } from "react-router-dom";
import { withdrawSchema } from "validations";

export const Form = () => {
  const data = useLoaderData();
  const coins = useMemo(
    () =>
      data?.length
        ? data.map((e) => ({ ...e, id: e?.name, name: e?.fullName }))
        : [],
    [data]
  );
  const [value, setValue] = useState(coins[0] || []);

  const submit = useSubmit();
  const action = useFormAction();

  const methods = useForm({ resolver: yupResolver(withdrawSchema) });
  const { handleSubmit } = methods;

  const onSubmit = (data) => {
    submit(data, {
      action,
      method: "post",
      encType: "application/x-www-form-urlencoded",
    });
  };
  return (
    <FormProvider {...methods}>
      <div className="rounded-[5px] flex-[5] h-fit bg-[var(--gray)]">
        <form
          onSubmit={handleSubmit(onSubmit)}
          className="py-12 px-[5.6rem] flex flex-col gap-7 lg:px-[4rem] lg:py-8 md:px-12 md:!py-6 sm:!px-4 sm:!pl-8"
        >
          <Box {...withdraw.forms[0]}>
            <Select
              {...{
                ...withdraw.forms[0]?.formElem,
                data: coins,
                selected: value,
                onChange: (v) => setValue(v),
              }}
            />
          </Box>
          <Box {...withdraw.forms[1]}>
            <Select
              {...{
                ...withdraw.forms[1]?.formElem,
                data: [value],
                selected: value,
              }}
            />
          </Box>
          <Box {...withdraw.forms[2]}>
            <Input {...withdraw.forms[2]?.formElem} style_type="my-outline" />
          </Box>
          <Box {...withdraw.forms[3]}>
            <Input {...withdraw.forms[3]?.formElem} style_type="my-outline" />
          </Box>

          <Btn type="submit" className="mt-4 py-4">
            Withdraw
          </Btn>
        </form>
        <div className="bg-[rgb(88_92_96_/_43%)] p-[28px_10px_24px_28px] flex gap-3 items-center border border-[#6d7277] rounded-[0_0_5px_5px] sm:p-[0.75rem_0.5rem_0.75rem_0.75rem]">
          <InformationCircleIcon className="w-6" />
          <p className="text-sm">
            The withdrawal normally completes within 30 min. If your transaction
            is still not completed within the indicated timeframe, please
            contact our customer support team for further assistance.
          </p>
        </div>
      </div>
    </FormProvider>
  );
};

const Box = ({ no, title, subtitle, children }) => {
  return (
    <div className="flex gap-5 relative">
      <div className="absolute right-[calc(100%_+_20px)] shrink-0 bg-[var(--green)] rounded-full w-6 h-6 flex items-center justify-center sm:w-4 sm:h-4 sm:top-1 sm:right-[calc(100%_+_8px)]">
        {no}
      </div>
      <div className="flex flex-col gap-2 w-full">
        <span className="text-lg font-medium">{title}</span>
        <p className="mb-3 text-xs text-gray-400">{subtitle}</p>
        {children}
      </div>
    </div>
  );
};
