import React from "react";
import img from "assets/images/home/ipad.png";
import { Btn } from "components/forms";
import { Link } from "react-router-dom";

export const Trade = () => {
  return (
    <div className="py-[var(--main-py)] flex flex-col gap-[var(--main-py)]">
      <div className="flex justify-between gap-5 lg:flex-col">
        <div className="max-w-[283px] relative">
          <div className="flex items-center gap-2 absolute bottom-[calc(100%_+_10px)]">
            <div className="w-6 h-[1px] bg-[#979797]" />
            <span className="text-xl text-[#939495] w-fit">
              Trade on the go
            </span>
          </div>
          <p className="font-semibold text-[2.3rem] leading-[2.6rem]">
            Trade anywhere and anytime with GitNeo
          </p>
        </div>
        <div className="flex flex-col gap-9">
          <p className="text-lg leading-[26px] text-[#aaabab] max-w-[515px]">
            Enjoy the convenience of GitNeos crypto trading platform. Make
            money on cryptocurrency anytime, anywhere as valuable
          </p>
          <Link to={"/deposit"}>
            <Btn className="h-fit w-fit p-[16px_39px]">Get Started</Btn>
          </Link>
        </div>
      </div>
      <img src={img} alt="" />
    </div>
  );
};
