import { InformationCircleIcon } from "@heroicons/react/24/outline";
import { yupResolver } from "@hookform/resolvers/yup";
import { Btn, CopyInput, Select } from "components/forms";
import { Modal } from "components/reusable";
import { deposit } from "contsants/deposit";
import { classes } from "helpers";
import { useMemo, useState } from "react";
import { FormProvider, useForm } from "react-hook-form";
import {
  useFormAction,
  useLoaderData,
  useLocation,
  useSubmit,
} from "react-router-dom";
import { depositSchema } from "validations";

export const Form = () => {
  const submit = useSubmit();
  const action = useFormAction();
  const location = useLocation();
  const data = useLoaderData();
  const coins = useMemo(
    () =>
      data?.length
        ? data.map((e) => ({
            ...e,
            id: e?.name,
            name: `${e?.fullName}${e?.subtitle ? ` (${e?.subtitle})` : ""}`,
          }))
        : [],
    [data]
  );

  const [isOpen, setIsOpen] = useState(false);
  const [value, setValue] = useState(
    (coins?.length && coins?.find((c) => c?.id === location.state?.coin_id)) ||
      coins[0] ||
      null
  );

  const methods = useForm({ resolver: yupResolver(depositSchema) });
  const { handleSubmit } = methods;

  const submitData = () => {
    submit(
      { ...value },
      {
        action,
        encType: "application/x-www-form-urlencoded",
        method: "post",
      }
    );
  };

  return (
    <>
      <FormProvider {...methods}>
        <div className="rounded-[5px] flex-[5] h-fit bg-[var(--gray)]">
          <form
            onSubmit={handleSubmit(() => {
              setIsOpen(true);
            })}
            className="py-12 px-[5.6rem] flex flex-col gap-7 lg:px-[4rem] lg:py-8 md:px-12 md:!py-6 sm:!px-4 sm:!pl-8"
          >
            <SelectBox
              {...deposit.forms[0]}
              formElem={{
                ...deposit.forms[0].formElem,
                data: coins,
                selected: value,
                onChange: (v) => setValue(v),
              }}
            >
              <>
                <span className="text-xs">Popular coins:</span>
                {!!coins?.length && (
                  <div className="grid grid-cols-[repeat(auto-fill,_minmax(50px,_1fr))] justify-items-center gap-[0.35rem_0.5rem]">
                    {coins?.map((el, i) => (
                      <div
                        key={i}
                        onClick={() => setValue(el)}
                        className={classes(
                          el?.id === value?.id && "bg-[rgba(131,140,151,0.9)]",
                          "w-full text-center border border-[#6d7277] bg-[rgb(88_92_96_/_43%)] rounded-md py-1 px-[0.6rem] cursor-pointer text-xs"
                        )}
                      >
                        {el?.id}
                      </div>
                    ))}
                  </div>
                )}
              </>
            </SelectBox>

            <SelectBox
              {...deposit.forms[1]}
              formElem={{
                ...deposit.forms[1].formElem,
                selected: value,
                data: [value],
              }}
            />
            <Btn type="submit" className="mt-4 w-fit">
              Show address
            </Btn>
          </form>
          <div className="bg-[rgb(88_92_96_/_43%)] p-[28px_10px_24px_28px] flex gap-3 items-center border border-[#6d7277] rounded-[0_0_5px_5px] sm:p-[0.75rem_0.5rem_0.75rem_0.75rem]">
            <InformationCircleIcon className="w-6" />
            <p className="text-sm">
              12 network confirmations are required before your funds will be
              added to your account balance.
            </p>
          </div>
        </div>
      </FormProvider>

      <Modal
        className="flex flex-col gap-5"
        title="Copy address/Scan QR code"
        subtitle="Go to your other wallet and paste the address that you copied or you can scan QR code from your mobile device"
        {...{ isOpen }}
        handleClose={() => setIsOpen(false)}
      >
        <CopyInput value={value?.address || ""} onCopy={submitData} />
      </Modal>
    </>
  );
};

const SelectBox = ({ no, title, subtitle, formElem, children }) => {
  return (
    <div className="flex gap-5 relative">
      <div className="absolute right-[calc(100%_+_20px)] shrink-0 bg-[var(--green)] rounded-full w-6 h-6 flex items-center justify-center sm:w-4 sm:h-4 sm:top-1 sm:right-[calc(100%_+_8px)]">
        {no}
      </div>
      <div className="flex flex-col gap-2 w-full">
        <span className="text-lg font-medium">{title}</span>
        <p className="mb-3 text-xs text-gray-400">{subtitle}</p>
        <Select {...formElem} />
        {children}
      </div>
    </div>
  );
};
