import { Container } from "components/main";
import { Benefits, Portfolio, Preface, Slider, Table, Trade } from "./blocks";
import instance from "request/api";
import { createContext, useContext, useEffect, useRef, useState } from "react";
import { userHandler } from "helpers";
import { useLoaderData } from "react-router-dom";

const interval_delay = 1000 * 60 * 5;

const HomeContext = createContext({
  ref: null,
  toCoinsTable: () => {},
  coins: [],
});
export const useHomeContext = () => useContext(HomeContext);

const Component = () => {
  const ref = useRef(null);
  const laoderData = useLoaderData();
  const [data, setData] = useState(laoderData);

  useEffect(() => {
    const interval = setInterval(async () => {
      try {
        const res = await instance.get("coins/all");
        setData(res?.data);
      } catch (err) {
        console.log(err);
      }
    }, interval_delay);

    return () => {
      clearInterval(interval);
    };
  }, []);

  const toCoinsTable = () => {
    if (ref && ref?.current)
      ref?.current?.scrollIntoView({ behavior: "smooth" });
  };

  return (
    <HomeContext.Provider value={{ ref, toCoinsTable, coins: data }}>
      <Container className="flex flex-col gap-8">
        <Preface />
        <Slider />
        <Table />
        <Benefits />
        <Trade />
        <Portfolio />
      </Container>
    </HomeContext.Provider>
  );
};

const loader = async () => {
  try {
    const res = await instance.get("coins/all");
    return res?.data || null;
  } catch (err) {
    return err;
  }
};

const action = async ({ request }) => {
  const fd = await request.formData();
  const data = Object.fromEntries(fd);
  switch (data?.role) {
    case "logout": {
      const res = await instance.get("auth/logout");
      userHandler.clearToken();
      return res.data;
    }
    default: {
      return null;
    }
  }
};

export const Home = Object.assign(Component, { loader, action });
