import React from "react";

export const Title = ({ className = "", children }) => {
  return (
    <h2
      className={[
        "font-medium text-[2.3rem] leading-[2.1rem] text-white text-center mb-4",
        className,
      ]
        .filter(Boolean)
        .join(" ")}
    >
      {children}
    </h2>
  );
};
