import { Container } from "components/main";
import { Faq, Form, Info } from "./blocks";
import instance from "request/api";

const Component = () => {
  return (
    <Container className="flex gap-[2.125rem] lg:flex-col lg:gap-[1.85rem] md:py-0">
      <Form />
      <div className="flex flex-[4] flex-col gap-8 h-fit">
        <Info />
        <Faq />
      </div>
    </Container>
  );
};

const loader = async () => {
  try {
    const res = await instance.get("coins/deposit");
    return res.data;
  } catch (err) {
    return err;
  }
};

const action = async () => {
  return { success: false, message: "Error: something went wrong" };
};

export const Withdraw = Object.assign(Component, { loader, action });
