import { useAppContext } from "contexts";
import { useContext, useEffect, useMemo, useState } from "react";
import { UNSAFE_DataRouterStateContext, useActionData } from "react-router-dom";

const configs = {
  dur: 4000,
};

export const Hint = () => {
  const actData = useActionData();
  const [opacity, setOpacity] = useState(false);
  const [hidden, setHidden] = useState(false);
  const routesContext = useContext(UNSAFE_DataRouterStateContext);
  const { message } = useAppContext();

  const actionData = useMemo(
    () =>
      message
        ? message
        : routesContext.actionData
        ? Object.values(routesContext.actionData)[0]
        : actData,
    [actData, routesContext.actionData, message]
  );

  const response = useMemo(() => {
    if (actionData instanceof Error || actionData?.response?.status >= 300) {
      if (actionData?.response?.data?.message) {
        return { ...actionData?.response?.data, success: false };
      } else if (actionData?.response?.statusText) {
        return {
          ...actionData?.response,
          message: actionData?.response?.statusText,
          success: false,
        };
      } else if (actionData?.message) {
        return { ...actionData, success: false };
      }
      return { ...actionData, success: false };
    } else if (actionData?.statusText === "OK") {
      return {
        ...actionData,
        message: actionData?.message || actionData?.data?.message,
        success: true,
      };
    } else if (actionData?.success || actionData?.message) {
      return actionData;
    }
  }, [actionData]);

  const hide = () => {
    setOpacity(true);
  };
  useEffect(() => {
    if (response) {
      setHidden(false);
      const timeout = setTimeout(() => {
        setOpacity(false);
        setTimeout(() => setOpacity(true), configs.dur);
      }, 300);

      return () => {
        clearTimeout(timeout);
      };
    }
  }, [response]);

  useEffect(() => {
    if (!!opacity) {
      const tm = setTimeout(() => {
        setHidden(true);
      }, 300);

      return () => clearTimeout(tm);
    }
  }, [opacity]);

  if (hidden || !response) {
    return null;
  }

  return (
    <div
      style={{
        background: response?.success ? "green" : "red",
        opacity: opacity ? "0" : "1",
      }}
      className="duration-300 transition-all fixed top-[25px] text-white flex items-center gap-3 px-4 py-2 right-[25px] rounded shadow-cst"
    >
      <span className="whitespace-nowrap overflow-hidden max-w-[500px] text-ellipsis text-white">
        {response?.message}
      </span>

      <button
        className="text-xl h-4 leading-4 cursor-default"
        onClick={hide}
        style={{ fontFamily: "auto" }}
      >
        &times;
      </button>
    </div>
  );
};
