import { terms } from "contsants/terms";
import React from "react";

export const Terms = () => {
  console.log(terms);
  return (
    <div className="bg-[#D6D7DD] flex-[1] px-5 py-14">
      <div className="bg-[#FFFFFF] border border-[#DADADA] p-5 text-[#404053]">
        <ul className="flex flex-col gap-4">
          {!!terms?.length &&
            terms?.map(({ id, text }, i) => (
              <li key={i} className="flex gap-3">
                <span className="min-w-[2rem]">{id}</span>
                <p>{text}</p>
              </li>
            ))}
        </ul>
      </div>
    </div>
  );
};
