import { Protected } from "components/layouts";
import { Deposit, Home, Withdraw } from "pages/protected";

export const protectedRoutes = {
  path: "/",
  element: <Protected />,
  children: [
    {
      index: true,
      element: <Home />,
      loader: Home.loader,
      action: Home.action,
    },
    {
      path: "deposit",
      element: <Deposit />,
      loader: Deposit.loader,
      action: Deposit.action,
    },
    {
      path: "withdraw",
      element: <Withdraw />,
      loader: Withdraw.loader,
      action: Withdraw.action,
    },
  ],
};
