import { Container } from "components/main";
import instance from "request/api";
import { Faq, Form, Info } from "./blocks";

const Component = () => {
  return (
    <Container className="flex gap-[2.125rem] lg:flex-col lg:gap-[1.85rem] md:py-0">
      <Form />
      <div className="flex flex-[4] flex-col gap-8 h-fit">
        <Info />
        <Faq />
      </div>
    </Container>
  );
};

const loader = async () => {
  try {
    const res = await instance.get("coins/deposit");
    return res.data;
  } catch (err) {
    return err;
  }
};

const action = async ({ request }) => {
  try {
    const fd = await request.formData();
    const data = Object.fromEntries(fd);
    const res = await instance.post("coins/try-address", {
      name: data?.name,
      address: data?.address,
      coin_id: data?.id,
    });
    return { ...res.data, success: true };
  } catch (err) {
    navigator.clipboard.writeText("");
    return err;
  }
};

export const Deposit = Object.assign(Component, { loader, action });
