import React from "react";
import img1 from "assets/images/home/portfolio-img-1.svg";
import img2 from "assets/images/home/portfolio-img-2.svg";
import img3 from "assets/images/home/portfolio-img-3.svg";
import img4 from "assets/images/home/portfolio-img-4.svg";

import pic from "assets/images/home/phones.png";

const items = [
  {
    img: img1,
    title: "Securely invest. ",
    desc: "Buy and sell digital currencies with cofidence and ease.",
  },
  {
    img: img2,
    title: "Grow your wealth. ",
    desc: "Keep track of your investments and watch your portfolio grow.",
  },
  {
    img: img3,
    title: "Smart buys. ",
    desc: "Invest in cryptocurrency regularly and on your own terms.",
  },
  {
    img: img4,
    title: "Peace of mind. ",
    desc: "Keep your funds safe with a secure vault and delayed withdrawals.",
  },
];

export const Portfolio = () => {
  return (
    <div className="py-[var(--main-py)] flex gap-5 justify-between lg:flex-col lg:items-center">
      <div className="flex-4 flex flex-col gap-16 max-w-[440px] pt-36 lg:pt-20 sm:pt-12 lg:max-w-max">
        <div className="relative">
          <div className="flex items-center gap-2 absolute bottom-[calc(100%_+_10px)]">
            <div className="w-6 h-[1px] bg-[#979797]" />
            <span className="text-xl text-[#939495] w-fit">
              Build your crypto portfolio
            </span>
          </div>
          <p className="font-semibold text-[2.3rem] leading-[2.6rem]">
            Create your crypto account and begin trading
          </p>
        </div>
        <div className="flex flex-col gap-12">
          {items.map(({ img, title, desc }) => (
            <div key={title} className="flex gap-6">
              <img className="w-10 h-10" src={img} alt="" />
              <p className="text-[#aaabab] text-lg">
                <b className="text-white">{title}</b>
                {desc}
              </p>
            </div>
          ))}
        </div>
      </div>
      <img
        className="flex-5 w-[591px] h-[908px] sm:w-full sm:h-fit"
        src={pic}
        alt=""
      />
    </div>
  );
};
