import { withdraw } from "contsants/withdraw";

export const Faq = () => {
  return (
    <div className="bg-[var(--gray)] rounded-[5px] p-[34px_33px_35px_34px] md:p-[28px_22px]">
      {withdraw?.faqs.map((d) => (
        <Li key={d.title} {...d} />
      ))}
    </div>
  );
};

const Li = ({ title, desc }) => {
  return (
    <div className="mb-10 flex flex-col gap-[0.9rem] last:m-0">
      <span className="text-base font-semibold">{title}</span>
      <p className="text-[0.8rem] text-gray-400">{desc}</p>
    </div>
  );
};
