import { Disclosure } from "@headlessui/react";
import { Bars3Icon, XMarkIcon } from "@heroicons/react/24/outline";
import logo from "assets/icons/main-logo.png";
import { useLocation, useNavigate, useSubmit } from "react-router-dom";
import { Container } from "./container/Container";

const navigation = [
  { name: "Home", href: "/" },
  { name: "Withdraw", href: "/withdraw" },
  { name: "Deposit", href: "/deposit" },
  { name: "Log out", href: "/login", action: "logout" },
];

function classNames(...classes) {
  return classes.filter(Boolean).join(" ");
}

export function Header() {
  const location = useLocation();
  const navigate = useNavigate();
  const submit = useSubmit();

  const logout = () => {
    submit(
      { role: "logout" },
      {
        action: "/?index",
        encType: "application/x-www-form-urlencoded",
        method: "post",
      }
    );
  };
  return (
    <Disclosure as="header" className="bg-transparent">
      {({ open }) => (
        <>
          <Container>
            <div className="sm:flex-row-reverse relative flex h-16 items-center justify-between">
              <div className="static min-sm:absolute inset-y-0 left-0 flex items-center min-sm:hidden">
                {/* Mobile menu button*/}
                <Disclosure.Button className="relative inline-flex items-center justify-center rounded-md p-2 text-gray-400 hover:bg-gray-700 hover:text-white focus:outline-none focus:ring-2 focus:ring-inset focus:ring-white">
                  <span className="absolute -inset-0.5" />
                  <span className="sr-only">Open main menu</span>
                  {open ? (
                    <XMarkIcon className="block h-6 w-6" aria-hidden="true" />
                  ) : (
                    <Bars3Icon className="block h-6 w-6" aria-hidden="true" />
                  )}
                </Disclosure.Button>
              </div>
              <div className="flex-[inherit] flex min-sm:flex-1 items-center justify-center min-sm:items-stretch min-sm:justify-between">
                <div className="flex shrink-0 items-center">
                  <img
                    className="h-8 min-w-full shrink-0"
                    src={logo}
                    alt="GitNeo"
                  />
                </div>
                <div className="hidden min-sm:ml-10 min-sm:block">
                  <div className="flex space-x-4">
                    {navigation.map((item) => (
                      <span
                        key={item.name}
                        onClick={() => {
                          if (item.action === "logout") logout();
                          else navigate(item.href);
                        }}
                        className={classNames(
                          location.pathname === item.href &&
                            "text-white bg-[var(--gray)] font-bold",
                          "rounded-md duration-300 hover:bg-[var(--gray)] text-gray-300 hover:text-white cursor-pointer px-3 py-2 text-sm font-medium",
                          item.className
                        )}
                      >
                        {item.name}
                      </span>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          </Container>

          <Disclosure.Panel className="min-sm:hidden shadow-xl">
            <div className="px-2 pb-3 pt-2">
              {navigation.map((item) => (
                <Disclosure.Button
                  key={item.name}
                  onClick={() => {
                    if (item.action === "logout") logout();
                    else navigate(item.href);
                  }}
                  className={classNames(
                    location.pathname === item.href &&
                      "bg-[var(--gray)]",
                    "rounded-md hover:bg-[var(--gray)] text-white duration-300 cursor-pointer w-full px-3 py-2 text-base font-medium"
                  )}
                >
                  {item.name}
                </Disclosure.Button>
              ))}
            </div>
          </Disclosure.Panel>
        </>
      )}
    </Disclosure>
  );
}
