import { useNavigate } from "react-router-dom";
import styles from "./btn.module.css";
import { classes } from "helpers";

export const Btn = ({
  type = "button",
  className = "",
  btn_style,
  children,
  size = "base",
  href,
  ...props
}) => {
  const navigate = useNavigate();
  return (
    <button
      {...props}
      className={classes(
        styles.button,
        className,
        btn_style === "border"
          ? "!bg-transparent border border-[#7ad1b3]"
          : btn_style === "transparent"
          ? "!bg-transparent border-none"
          : "",
        size === "sm" ? "min-h-[1rem] text-sm px-3" : ""
      )}
      onClick={(e) => {
        href && navigate(href);
        props?.onClick?.(e);
      }}
      type={type}
    >
      {children}
    </button>
  );
};
