import React, { useId } from "react";
import { ErrorMessage } from "components/reusable";
import { useError, useFormRegister } from "hooks";

export const Checkbox = ({
  color = "white",
  children,
  name,
  value,
  title,
  desc,
  ...props
}) => {
  const id = useId();
  const error = useError(name);
  const register = useFormRegister(name);

  return (
    <div className="relative flex gap-x-3">
      <div className="flex items-center">
        <input
          type="checkbox"
          {...{ name, value, ...props }}
          {...register}
          id={id}
          className="w-5 h-5 rounded bg-[var(--main-html-bg)] border-gray-300 !outline-none text-[var(--green)] !ring-transparent"
        />
      </div>
      <div className={`text-sm leading-6`}>
        <label htmlFor={id} className="font-medium">
          {title}
        </label>
        {desc && <p>{desc}</p>}
      </div>

      {error && <ErrorMessage>{error}</ErrorMessage>}
    </div>
  );
};
