import { withdraw } from "contsants/withdraw";

export const Info = () => {
  return (
    <div className="bg-[var(--gray)] rounded-[5px] flex flex-col gap-10 relative p-[44px_90px_44px_38px] md:p-[32px_25px]">
      <svg
        width="45"
        height="43"
        viewBox="0 0 45 43"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
        className="absolute top-0 right-0"
      >
        <path
          d="M39.6552 1H2L44 41V4.21429V3.85714V3.5L43.6379 2.78571L43.2759 2.07143L42.9138 1.71429L42.5517 1.35714L41.8276 1H39.6552Z"
          fill="#68BC4A"
          stroke="#68BC4A"
        ></path>
      </svg>
      <div className="relative w-fit">
        <span className="text-lg">Important information</span>
        <div className="underline" />
      </div>
      <ul className="list-disc pl-5">
        {withdraw?.info.map((text, i) => (
          <li key={i} className="text-sm mb-2 text-gray-400">
            {text}
          </li>
        ))}
      </ul>
    </div>
  );
};
